<template>
  <div v-if="webDavError != null">
    <Modal
      :modal-title="$t('phrases.modals.errorTitle')"
      @close-modal="closeModal"
    >
      <template>
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-4">
              <p>{{ $t("phrases.errors.errorMessage") }}:</p>
            </div>
            <div class="col-md-8">
              <p class="error-message" v-html="webDavError.ErrorMessage" />
            </div>
          </div>
          <div class="row error-details-row">
            <div class="col-md-12">
              <p>
                <button
                  class="btn btn-light"
                  type="button"
                  @click.prevent="isOpenedDetails = !isOpenedDetails"
                >
                  {{ $t("phrases.errors.errorDetails") }}
                </button>
              </p>
              <div
                id="error-details-collapse"
                class="collapse"
                :class="{ show: isOpenedDetails }"
              >
                <div class="card card-body">
                  <div class="row">
                    <div class="col-md-2">
                      <p>{{ $t("phrases.url") }}:</p>
                    </div>
                    <div class="col-md-10">
                      <p
                        class="error-details-url"
                        v-html="htmlEscape(webDavError.Error.Uri)"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-md-4 ">
                      <p>{{ $t("phrases.errors.errorMessage") }}:</p>
                    </div>
                    <div class="col-md-8">
                      <p
                        class="error-details-message"
                        v-html="formatMessage(webDavError.Error.Message)"
                      />
                    </div>
                  </div>
                  <div class="row error-details-row">
                    <div class="col-md-12">
                      <iframe style="width: 100%; border: none" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
      <template v-slot:modal-footer>
        <button type="button" class="btn btn-light" @click.prevent="closeModal">
          {{ $t("phrases.close") }}
        </button>
      </template>
    </Modal>
  </div>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  emits: ["close-modal"],
  data() {
    return {
      isOpenedDetails: false
    };
  },
  computed: {
    webDavError() {
      return this.$store.state.grid.Error;
    }
  },
  methods: {
    htmlEscape(sText) {
      return CommonService.Formatters.HtmlEscape(sText);
    },
    formatMessage(sMessage) {
      return this.htmlEscape(sMessage)
        .replace(/\n/g, "<br />\n")
        .replace(/\t/g, "&nbsp;&nbsp;&nbsp;&nbsp;");
    },
    closeModal() {
      this.$store.commit("clearError");
    }
  }
};
</script>
