<template>
  <form @submit.prevent="validFormData">
    <Modal
      :modal-title="$t('phrases.modals.createFolderTitle')"
      @close-modal="closeModal"
    >
      <template>
        <div class="form-group">
          <input
            v-model="folderName"
            v-focus
            type="text"
            class="form-control"
            :placeholder="$t('phrases.modals.folderNamePlaceholder')"
          />
          <div v-if="errorMessage" class="alert alert-danger">
            {{ errorMessage }}
          </div>
        </div>
      </template>
      <template v-slot:modal-footer>
        <button type="submit" class="btn btn-primary btn-submit">
          {{ $t("phrases.ok") }}
        </button>
        <button
          type="button"
          class="btn btn-light"
          data-dismiss="modal"
          @click.prevent="closeModal"
        >
          {{ $t("phrases.cancel") }}
        </button>
      </template>
    </Modal>
  </form>
</template>
<script>
import WebDavService from "@/services/WebDavService.js";
import CommonService from "@/services/CommonService.js";
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  emits: ["close-modal"],
  data() {
    return {
      folderName: "",
      errorMessage: ""
    };
  },
  methods: {
    /**
     * Close modal event
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * Validate data
     */
    validFormData() {
      if (this.folderName != null && this.folderName.match(/^ *$/) === null) {
        this.errorMessage = CommonService.Validators.ValidateName(
          this.folderName
        );
        if (!this.errorMessage) {
          this.createFolder();
        }
      } else {
        this.errorMessage = this.$t("phrases.validations.nameIsRequired");
      }
    },
    /**
     * Create folder
     */
    createFolder() {
      WebDavService.createFolder(
        this.$store.state.grid.CurrentFolder,
        this.folderName
      )
        .then(() => {
          this.$store.commit("updateGrid");
          this.$emit("close-modal");
        })
        .catch(resp => {
          if (
            resp.Error instanceof
            window.ITHit.WebDAV.Client.Exceptions.MethodNotAllowedException
          ) {
            this.errorMessage = resp.Error.Error.Description
              ? resp.Error.Error.Description
              : this.$t("phrases.validations.folderExists");
          }
        });
    }
  }
};
</script>
