<template>
  <div v-if="loading">
    <SceletonGridContainer :count-items="items.length" />
  </div>
  <div v-else ref="ithitDropzone" class="ithit-grid-wrapper">
    <div class="drop-files-header">
      <div class="drop-files-title">
        <i class="icon icon-upload-items"></i>{{ $t("phrases.grid.dragFiles") }}
      </div>
    </div>
    <div class="table-responsive">
      <table class="table table-hover ithit-grid-container">
        <thead>
          <tr>
            <th scope="col">
              <label class="custom-checkbox"
                ><input v-model="allSelected" type="checkbox"/><span
                  class="checkmark"
              /></label>
            </th>
            <th scope="col" />
            <TableSortHeader
              class="ellipsis sort"
              scope="col"
              sort-column="displayname"
            >
              <span>{{ $t("phrases.grid.tableHeader.displayName") }}</span>
            </TableSortHeader>
            <TableSortHeader
              class="d-none d-xl-table-cell sort"
              scope="col"
              sort-column="getcontenttype"
            >
              {{ $t("phrases.grid.tableHeader.type") }}
            </TableSortHeader>
            <TableSortHeader
              class="sort"
              scope="col"
              sort-column="quota-used-bytes"
            >
              {{ $t("phrases.grid.tableHeader.size") }}
            </TableSortHeader>
            <TableSortHeader
              class="d-none d-lg-table-cell sort"
              scope="col"
              sort-column="getlastmodified"
            >
              {{ $t("phrases.grid.tableHeader.modified") }}
            </TableSortHeader>
            <th class="column-action" scope="col" />
          </tr>
        </thead>
        <tbody>
          <template v-for="(item, index) in items">
            <GridTableRow
              :key="index"
              :item-selected="item.Selected"
              :item.sync="item"
              :class="{ hover: hoveredItemIndex == index }"
            />
            <tr
              v-if="searchMode"
              :key="'snippet' + index"
              class="tr-snippet-url"
              @mouseover="snippetMouseOverHandler(index)"
              @mouseleave="snippetMouseLeaveHandler()"
            >
              <td class="d-none d-xl-table-cell" />
              <td class="d-none d-lg-table-cell" />
              <td colspan="10">
                <SnippetSearch :item="item" />
              </td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import SnippetSearch from "@/components/SnippetSearch.vue";
import SceletonGridContainer from "@/components/grid/SceletonGridContainer.vue";
import TableSortHeader from "@/components/grid/TableSortHeader.vue";
import GridTableRow from "@/components/grid/GridTableRow.vue";

export default {
  components: {
    SnippetSearch,
    SceletonGridContainer,
    TableSortHeader,
    GridTableRow
  },
  data() {
    return {
      allSelected: false,
      hoveredItemIndex: null
    };
  },
  computed: {
    items() {
      return this.$store.state.grid.Items;
    },
    searchMode() {
      return this.$store.state.grid.SearchQuery != "";
    },
    loading() {
      return this.$store.state.grid.GridLoading;
    }
  },
  watch: {
    allSelected: function(value) {
      this.setSelectedForAllItems(value);
    },
    items: function() {
      this.allSelected = false;
      this.setSelectedForAllItems(false);
      this.$store.commit("setGridLoading", false);
    }
  },
  created() {
    this.setSelectedForAllItems(false);
    this.$store.commit("setGridLoading", false);
  },
  methods: {
    /**
     * Set 'Selected' property for all items
     * @param {Boolean} isSelected
     */
    setSelectedForAllItems(isSelected) {
      this.items.forEach(element => {
        element.Selected = isSelected;
      });
      this.$store.commit("clearSelectedItems");
      if (isSelected) {
        this.items.forEach(element => {
          this.$store.commit("pushToSelectedItems", element);
        });
      }
    },
    snippetMouseOverHandler(index) {
      this.hoveredItemIndex = index;
    },
    snippetMouseLeaveHandler() {
      this.hoveredItemIndex = null;
    }
  }
};
</script>
