<template>
  <div v-if="rewriteItemsData">
    <Modal
      :modal-title="$t('phrases.modals.defaultModalTitle')"
      @close-modal="closeModal"
    >
      <template>
        <p class="message">
          {{ $t("phrases.validations.followingItemExist") }}:
        </p>
        <p class="message" v-html="decodeURI(rewriteItemsData.ItemsList)"></p>
        <p class="message">{{ $t("phrases.overwrite") }}?</p>
      </template>
      <template v-slot:modal-footer>
        <button
          type="button"
          class="btn btn-primary btn-ok"
          @click.prevent="submitModal"
        >
          {{ $t("phrases.yesToAll") }}
        </button>
        <button
          type="button"
          class="btn btn-light"
          @click.prevent="unsubmitModal"
        >
          {{ $t("phrases.noToAll") }}
        </button>
        <button
          type="button"
          class="btn btn-light"
          data-dismiss="modal"
          @click.prevent="closeModal"
        >
          {{ $t("phrases.cancel") }}
        </button>
      </template>
    </Modal>
  </div>
</template>
<script>
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  emits: ["close-modal"],
  computed: {
    rewriteItemsData() {
      return this.$store.state.uploader.RewriteItemsData;
    }
  },
  methods: {
    submitModal() {
      this.rewriteItemsData.OnOverwrite();
      this.closeModal();
    },
    unsubmitModal() {
      this.rewriteItemsData.OnSkipExists();
      this.closeModal();
    },
    closeModal() {
      this.$store.commit("SetRewriteItemsData", null);
    }
  }
};
</script>
