<template>
  <nav aria-label="breadcrumb">
    <div class="ithit-breadcrumb-container">
      <router-link
        v-if="!isSearchMode"
        :to="parts.length >= 2 ? getHref(parts.length - 2) : '/'"
        class="btn-tool"
        title="Up one level"
        :disabled="!parts.length"
        ><i class="icon icon-up-one-level"></i
      ></router-link>
      <ol class="breadcrumb">
        <li v-if="!isSearchMode" class="breadcrumb-item">
          <router-link to="/">
            <span class="icon icon-home" />
          </router-link>
        </li>
        <li
          v-for="(item, index) in parts"
          :key="index"
          class="breadcrumb-item"
          :class="index == parts.length - 1 ? 'active' : ''"
        >
          <router-link v-if="index != parts.length - 1" :to="getHref(index)">
            <span>{{ decodeURIComponent(item) }}</span>
          </router-link>
          <span v-else>{{ decodeURIComponent(item) }}</span>
        </li>
      </ol>
    </div>
  </nav>
</template>
<script>
export default {
  name: "Breadcrumb",
  props: {
    sUrl: {
      type: String,
      required: true
    },
    isSearchMode: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      parts: []
    };
  },
  created() {
    this.getParts(this.sUrl);
  },
  methods: {
    /**
     * Gets Breadcrumb item href
     */
    getHref(index) {
      return "/" + this.parts.slice(0, index + 1).join("/") + "/";
    },
    /**
     * Gets Breadcrumb parts
     */
    getParts(sUrl) {
      this.parts = sUrl
        .split("/")
        .slice()
        .filter(function(v) {
          return v;
        });
    }
  }
};
</script>
