<template>
  <button
    :id="config.id"
    class="btn-tool"
    :class="{ 'hide-disabled-sm': toolbarConfig.hideDisabledOnMobile }"
    :title="config.title"
    @click.prevent="onClick"
  >
    <i class="icon " :class="config.iconClassName"></i
    ><span v-html="config.innerHtml"></span>
  </button>
</template>
<script>
import ToolbarConfig from "@/sources/toolbar.js";
export default {
  emits: ["click-event"],
  props: {
    config: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      toolbarConfig: ToolbarConfig
    };
  },
  methods: {
    /**
     * On click button event
     */
    onClick() {
      this.$emit("click-event");
    }
  }
};
</script>
