<template>
  <div v-if="uploadingItems.length && isShowUploading" class="uploading">
    <div
      class="progress-wrapper"
      @mouseover="uploadingHover = true"
      @mouseleave="uploadingHover = false"
    >
      <div class="progress">
        <div
          class="progress-bar"
          role="progressbar"
          :style="'width:' + uploadedPersent + '%'"
          :aria-valuenow="uploadedPersent"
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      </div>
    </div>
    <div
      v-show="!showDetails"
      class="uploading-block"
      :class="{ show: uploadingHover || isFirstShowBlock }"
    >
      <div class="uploading-controls">
        {{ $t("phrases.uploader.uploaded") }}
        <span class="persent">{{ uploadedPersent }}%</span>
        <button
          v-if="!isPaused"
          class="btn-transparent float-right"
          :disabled="disabledPauseButton"
          title="Pause upload"
          @click.prevent="pauseAll"
        >
          <i class="icon icon-pause"></i>
        </button>
        <button
          v-else
          class="btn-transparent float-right"
          :disabled="disabledPauseButton"
          title="Resume upload"
          @click.prevent="playAll"
        >
          <i class="icon icon-play"></i>
        </button>
      </div>
      <div>
        <button class="btn btn-primary" @click.prevent="toggleDetails">
          {{ $t("phrases.details") }}
        </button>
      </div>
    </div>
    <div v-show="showDetails">
      <div
        v-draggable
        class="uploading-details"
        style="position: absolute !important"
      >
        <div class="details-header text-center">
          <span class="details-title">
            {{ $t("phrases.uploader.filesUpload") }}</span
          >
          <button
            class="btn-transparent float-right"
            :disabled="disabledPauseButton"
            :title="$t('phrases.close')"
            @click.prevent="toggleDetails"
          >
            <i class="icon icon-close"></i>
          </button>
        </div>
        <div class="uploading-items">
          <UploadingItem
            v-for="(item, index) in uploadingItems"
            :key="'uploading item ' + index"
            :upload-item-row="item"
          />
          <div class="uploading-footer">
            <button
              v-if="!isPaused"
              class="btn btn-outline-primary"
              :disabled="disabledPauseButton"
              :title="$t('phrases.uploader.pauseUpload')"
              @click.prevent="pauseAll"
            >
              {{ $t("phrases.uploader.pauseUpload") }}
            </button>
            <button
              v-else
              class="btn btn-outline-primary"
              :disabled="disabledPauseButton"
              :title="$t('phrases.uploader.resumeUpload')"
              @click.prevent="playAll"
            >
              {{ $t("phrases.uploader.resumeUpload") }}
            </button>
            <button
              class="btn btn-primary"
              :title="$t('phrases.uploader.cancelAllUpload')"
              @click.prevent="cancelAll"
            >
              {{ $t("phrases.uploader.cancelAllUpload") }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import UploadingItem from "@/components/uploader/UploadingItem.vue";
import { Draggable } from "draggable-vue-directive";
export default {
  components: {
    UploadingItem
  },
  directives: {
    Draggable
  },
  data() {
    return {
      showDetails: false,
      disabledPauseButton: false,
      isPaused: false,
      uploadingHover: false,
      isFirstShowBlock: true
    };
  },
  computed: {
    uploadingItems() {
      return this.$store.state.uploader.UploadingItems;
    },
    uploadedPersent() {
      var uploaded = 0;
      if (this.uploadingItems.length) {
        this.uploadingItems.forEach(element => {
          uploaded += element.UploadItem.GetProgress().Completed;
        });
        uploaded /= this.uploadingItems.length;
      }

      return Math.round(uploaded);
    },
    isShowUploading() {
      var isShow = false;
      this.uploadingItems.forEach(el => {
        if (
          !(el.CurrentState === "Completed" || el.CurrentState === "Canceled")
        ) {
          isShow = true;
        } else {
          this.$store.commit("updateGrid");
        }
      });
      return isShow;
    }
  },
  watch: {
    isShowUploading(val) {
      if (val) {
        this.setIsFirstShowBlock(false);
      } else {
        this.resetData();
      }
    }
  },
  methods: {
    toggleDetails() {
      this.showDetails = !this.showDetails;
    },
    pauseAll() {
      this.uploadingItems.forEach(el => {
        el._PauseClickHandler();
      });
      this.isPaused = true;
    },
    playAll() {
      this.uploadingItems.forEach(el => {
        if (el.CurrentState == "Paused") {
          el._PlayClickHandler();
        }
      });
      this.isPaused = false;
    },
    cancelAll() {
      this.uploadingItems.forEach(el => {
        el._CancelClickHandler();
      });
    },
    setIsFirstShowBlock(val) {
      setTimeout(() => (this.isFirstShowBlock = val), 3000);
    },
    resetData() {
      this.disabledPauseButton = false;
      this.isPaused = false;
      this.uploadingHover = false;
      this.sFirstShowBlock = true;
    }
  }
};
</script>
