<template>
  <form @submit.prevent="validFormData">
    <Modal
      :modal-title="$t('phrases.modals.renameItemTitle')"
      @close-modal="closeModal"
    >
      <template>
        <div class="form-group">
          <input
            v-model="newItemName"
            v-focus
            type="text"
            class="form-control"
            :placeholder="$t('phrases.modals.itemNamePlaceholder')"
          />
          <div v-if="errorMessage" class="alert alert-danger">
            {{ errorMessage }}
          </div>
        </div>
      </template>
      <template v-slot:modal-footer>
        <button type="submit" class="btn btn-primary btn-submit">
          {{ $t("phrases.ok") }}
        </button>
        <button
          type="button"
          class="btn btn-light"
          data-dismiss="modal"
          @click.prevent="closeModal"
        >
          {{ $t("phrases.cancel") }}
        </button>
      </template>
    </Modal>
  </form>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  emits: ["close-modal"],
  data() {
    return {
      newItemName: "",
      oldItemName: "",
      errorMessage: ""
    };
  },
  computed: {
    selectedItems() {
      return this.$store.state.grid.SelectedItems;
    }
  },
  created() {
    if (this.selectedItems.length) {
      this.oldItemName = this.selectedItems[0].DisplayName;
      this.newItemName = this.selectedItems[0].DisplayName;
    }
  },
  methods: {
    /**
     * Close modal event
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * validate data
     */
    validFormData() {
      if (this.oldItemName == this.newItemName) {
        this.$emit("close-modal");
      } else if (
        this.newItemName != null &&
        this.newItemName.match(/^ *$/) === null
      ) {
        this.errorMessage = CommonService.Validators.ValidateName(
          this.newItemName
        );
        if (!this.errorMessage) {
          this.renameItem();
        }
      } else {
        this.errorMessage = this.$t("phrases.validations.nameIsRequired");
      }
    },
    /**
     * Rename Item
     */
    renameItem() {
      this.$emit("close-modal");
      this.$store.commit("renameSelectedItem", this.newItemName);
    }
  }
};
</script>
