import { WebDavStore } from "@/stores/WebDavStore.js";
import UrlResolveService from "@/services/UrlResolveService.js";
import Router from "@/router/index.js";

export default {
  run() {
    var connection = new WebSocket(this._getConnectionUrl());
    connection.onmessage = function(event) {
      var notifyObject = JSON.parse(event.data);

      // Removing domain and trailing slash.
      var currentLocation = location.pathname.replace(/^\/|\/$/g, "");
      // Checking message type after receiving.
      if (notifyObject.EventType === "refresh") {
        // Refresh folder structure if any item in this folder is updated or new item is created.
        if (
          currentLocation.toUpperCase() ===
          notifyObject.FolderPath.toUpperCase()
        ) {
          WebDavStore.commit("updateGrid");
        }
      } else if (notifyObject.EventType === "delete") {
        if (
          notifyObject.FolderPath.substring(
            0,
            notifyObject.FolderPath.lastIndexOf("/")
          ).toUpperCase() === currentLocation.toUpperCase()
        ) {
          // Refresh folder structure if any item in this folder is deleted.
          WebDavStore.commit("updateGrid");
        } else if (
          currentLocation
            .toUpperCase()
            .indexOf(notifyObject.FolderPath.toUpperCase()) === 0
        ) {
          var rootFolder = UrlResolveService.getRootFolder();
          // Redirect client to the root folder if current path is being deleted.
          Router.push(rootFolder ? rootFolder : "/");
        }
      }
    };
  },
  _getConnectionUrl() {
    var url = new URL(UrlResolveService.getRootUrl());
    if (url.protocol === "https:") {
      return "wss://" + url.hostname;
    } else {
      return "ws://" + url.hostname;
    }
  }
};
