<template>
  <div>
    <button
      v-if="item.IsFolder"
      class="btn btn-primary btn-sm btn-labeled"
      type="button"
      :disabled="isDisabledAction"
      @click="openFolderInOsFileManager()"
    >
      <span class="btn-label"><i class="icon icon-open-folder"/></span>
      <span class="d-none d-lg-inline-block">{{ $t("phrases.browse") }}</span>
    </button>
    <div v-else class="btn-group">
      <button
        type="button"
        class="btn btn-primary btn-sm btn-labeled btn-default-edit"
        :disabled="isDisabledAction"
        @click.prevent="editDocument()"
      >
        <span class="btn-label"><i class="icon icon-edit"/></span>
        <span class="d-none d-lg-inline-block btn-edit-label">{{
          $t("phrases.edit")
        }}</span>
      </button>
      <button
        type="button"
        class="btn btn-primary dropdown-toggle dropdown-toggle-split btn-sm"
        :disabled="isDisabledAction"
        @click.prevent="isShowMenu = !isShowMenu"
      >
        <span class="sr-only">Toggle Dropdown</span>
      </button>
      <v-popover
        placement="left-end"
        offset="1"
        :open="isShowMenu"
        popover-class="mb-3"
        @apply-hide="isShowMenu = false"
      >
        <template slot="popover">
          <div class="dropdown-menu actions show">
            <a
              class="dropdown-item"
              href="javascript:void()"
              :title="$t('phrases.grid.actions.editWithDesctopApp.title')"
              @click.prevent="editDocument()"
              ><i class="icon icon-edit-associated" />{{
                $t("phrases.grid.actions.editWithDesctopApp.text")
              }}
            </a>
            <div class="dropdown-divider" />
            <a
              class="dropdown-item desktop-app"
              href="javascript:void()"
              :title="$t('phrases.grid.actions.selectDesctopApp.title')"
              @click.prevent="openDocumentWith()"
              >{{ $t("phrases.grid.actions.selectDesctopApp.text") }}
            </a>
          </div>
        </template>
      </v-popover>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import VTooltip from "v-tooltip";
import webDavSettings from "@/WebDavSettings.js";
import UrlResolveService from "@/services/UrlResolveService.js";
import WebDavService from "@/services/WebDavService.js";
import ProtocolService from "@/services/ProtocolService.js";
Vue.use(VTooltip);
export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isShowMenu: false
    };
  },
  computed: {
    isDisabledAction() {
      return !(
        ProtocolService.isDavProtocolSupported() ||
        ProtocolService.isMicrosoftOfficeDocument(this.item.Href)
      );
    }
  },
  methods: {
    /**
     * Opens folder in operating system file manager
     */
    openFolderInOsFileManager() {
      WebDavService.DocManager.OpenFolderInOsFileManager(
        this.item.Href,
        UrlResolveService.getRootUrl(),
        this._protocolInstallMessage,
        null,
        webDavSettings.EditDocAuth.SearchIn,
        webDavSettings.EditDocAuth.CookieNames,
        webDavSettings.EditDocAuth.LoginUrl
      );
    },
    /**
     * Opens document for editing
     */
    editDocument() {
      if (
        webDavSettings.EditDocAuth.Authentication.toLowerCase() == "cookies"
      ) {
        WebDavService.DocManager.DavProtocolEditDocument(
          this.item.Href,
          UrlResolveService.getRootUrl(),
          this._protocolInstallMessage,
          null,
          webDavSettings.EditDocAuth.SearchIn,
          webDavSettings.EditDocAuth.CookieNames,
          webDavSettings.EditDocAuth.LoginUrl
        );
      } else {
        WebDavService.DocManager.EditDocument(
          this.item.Href,
          UrlResolveService.getRootUrl(),
          this._protocolInstallMessage
        );
      }
    },
    /**
     * Selects application and opens document for editing
     */
    openDocumentWith() {
      WebDavService.DocManager.DavProtocolEditDocument(
        this.item.Href,
        UrlResolveService.getRootUrl(),
        this._protocolInstallMessage,
        null,
        webDavSettings.EditDocAuth.SearchIn,
        webDavSettings.EditDocAuth.CookieNames,
        webDavSettings.EditDocAuth.LoginUrl,
        "OpenWith"
      );
    },
    _protocolInstallMessage() {
      this.$store.commit("showProtocolModal");
    }
  }
};
</script>
