<template>
  <div v-if="webDavProtocol && showModal">
    <Modal
      :modal-title="$t('phrases.modals.downloadProtocolTitle')"
      modal-dialog-css-class="modal-lg"
      @close-modal="closeModal"
    >
      <template>
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-12">
              <p>
                {{ $t("phrases.downloadProtocol.installCustomProtocol") }}
              </p>
              <ol>
                <li>
                  {{
                    $t("phrases.downloadProtocol.downloadAndInstallFiles")
                  }}:<br />
                  <span class="current-os" />
                  <span v-if="webDavProtocol.CurrentOs" class="current-os">
                    <span :class="webDavProtocol.CurrentOs.CssClass">
                      <span v-html="webDavProtocol.CurrentOs.Name" /><a
                        target="_blank"
                        :href="webDavProtocol.CurrentOs.DownloadLink"
                        >{{ webDavProtocol.CurrentOs.FileName }}</a
                      ><br />
                    </span>
                  </span>
                  <a
                    href="javascript:void(0)"
                    class="more-lnk"
                    @click.prevent="toggleMoreOs"
                    ><span>{{ moreOsDisplayed ? "- " : "+" }}</span>
                    {{ $t("phrases.otherOs") }}:</a
                  >
                  <p class="more-pnl" :class="{ 'd-block': moreOsDisplayed }">
                    <span
                      v-for="(item, index) in webDavProtocol.OtherOs"
                      :key="index"
                      :class="item.CssClass"
                    >
                      <span v-html="item.Name" /><a
                        target="_blank"
                        :href="item.DownloadLink"
                        >{{ item.FileName }}</a
                      ><br />
                    </span>
                  </p>
                </li>
                <li>
                  {{
                    $t(
                      "phrases.downloadProtocol.enableITHitEditDocumentOpener"
                    )
                  }}:<br />
                  <span
                    class="not-required-internet-explorer"
                    style="display:none"
                  >
                    {{
                      $t(
                        "phrases.downloadProtocol.notRequiredForInternetExplorer"
                      )
                    }}.<br />
                  </span>
                  <span
                    v-if="webDavProtocol.CurrentBrowser"
                    class="current-browser"
                  >
                    <span :class="webDavProtocol.CurrentBrowser.CssClass">
                      <span v-html="webDavProtocol.CurrentBrowser.Name" />
                      <a
                        v-if="webDavProtocol.CurrentBrowser.DownloadLink"
                        target="_blank"
                        :href="webDavProtocol.CurrentBrowser.DownloadLink"
                        >{{ webDavProtocol.CurrentBrowser.FileName }}</a
                      ><br />
                    </span>
                  </span>
                  <a
                    href="javascript:void(0)"
                    class="more-lnk"
                    @click.prevent="toggleMoreBrowsers"
                    ><span>{{ moreBrowsersDisplayed ? "- " : "+" }}</span>
                    {{ $t("phrases.otherWebBrowsers") }}:</a
                  >
                  <p
                    class="more-pnl"
                    :class="{ 'd-block': moreBrowsersDisplayed }"
                  >
                    <span
                      v-for="(item, index) in webDavProtocol.OtherBrowsers"
                      :key="index"
                      :class="item.CssClass"
                    >
                      <a target="_blank" :href="item.DownloadLink">{{
                        item.FileName
                      }}</a
                      ><br />
                    </span>
                  </p>
                </li>
              </ol>
              <br />
              {{ $t("phrases.see") }}
              <a
                href="https://www.webdavsystem.com/ajax/programming/open-doc-webpage/install/windows/"
                target="_blank"
                >{{ $t("phrases.downloadProtocol.howInstallProtocolApp") }}</a
              >
              {{ $t("phrases.and") }}&nbsp;
              <a
                href="https://www.webdavsystem.com/ajax/programming/open-doc-webpage/install/web_browser_extensions/"
                target="_blank"
                >{{
                  $t("phrases.downloadProtocol.installAndVerifyExtensions")
                }}.</a
              >
            </div>
          </div>
        </div>
      </template>
      <template v-slot:modal-footer>
        <button type="button" class="btn btn-light" @click.prevent="closeModal">
          {{ $t("phrases.close") }}
        </button>
      </template>
    </Modal>
  </div>
</template>
<script>
import ProtocolService from "@/services/ProtocolService.js";
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  data() {
    return {
      webDavProtocol: ProtocolService.getProtocol(),
      moreOsDisplayed: false,
      moreBrowsersDisplayed: false
    };
  },
  computed: {
    showModal() {
      return this.$store.state.grid.ShowProtocolModal;
    }
  },
  methods: {
    /**
     * Closes modal
     */
    closeModal() {
      this.$store.commit("hideProtocolModal");
    },
    /**
     * Shows or hides "Other Os" block
     */
    toggleMoreOs() {
      this.moreOsDisplayed = !this.moreOsDisplayed;
    },
    /**
     * Shows or hides "Other Browsers" block
     */
    toggleMoreBrowsers() {
      this.moreBrowsersDisplayed = !this.moreBrowsersDisplayed;
    }
  }
};
</script>
