<template>
  <div>
    <div class="modal show" tabindex="-1" role="dialog">
      <div class="modal-dialog" :class="modalDialogCssClass" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              {{ modalTitle }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
              @click.prevent="closeModal"
            >
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <slot></slot>
          </div>
          <div class="modal-footer">
            <slot name="modal-footer"></slot>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop show" />
  </div>
</template>
<script>
import { Escapable } from "@/mixins/Escapable.js";
export default {
  mixins: [Escapable],
  props: {
    modalDialogCssClass: {
      type: String,
      default: "modal-md"
    },
    modalTitle: {
      type: String,
      default: ""
    }
  },
  emits: ["close-modal"],
  methods: {
    /**
     * Close modal event
     */
    closeModal() {
      this.$emit("close-modal");
    },
    escapeHandler() {
      this.closeModal();
    }
  }
};
</script>
