<template>
  <Modal
    :modal-title="$t('phrases.modals.defaultModalTitle')"
    @close-modal="closeModal"
  >
    <template
      ><p class="message">
        {{ message }}
      </p>
    </template>
    <template v-slot:modal-footer>
      <button
        type="button"
        class="btn btn-primary btn-ok"
        @click.prevent="submitModal"
      >
        {{ $t("phrases.ok") }}
      </button>
      <button
        type="button"
        class="btn btn-light"
        data-dismiss="modal"
        @click.prevent="closeModal"
      >
        {{ $t("phrases.cancel") }}
      </button>
    </template>
  </Modal>
</template>
<script>
import Modal from "@/components/modals/Modal.vue";
export default {
  components: {
    Modal
  },
  emits: ["close-modal", "submit-modal"],
  props: {
    message: {
      type: String,
      required: true
    }
  },
  computed: {
    selectedItems() {
      return this.$store.state.grid.SelectedItems;
    }
  },
  methods: {
    /**
     * Close modal
     */
    closeModal() {
      this.$emit("close-modal");
    },
    /**
     * Submit Modal
     */
    submitModal() {
      this.$emit("submit-modal");
      this.$emit("close-modal");
    }
  }
};
</script>
